.header__logo {
  @include abs(20px, null, null, 20px);
  @include size(60px);

  background-image: url(./assets/img/logo/logo_white.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  font-size: .1px; 

  z-index: map-get($z-index, nav);

  @include mappy-query(portable) {
    top: 24px
  }

  .color--blue & {
    background-image: url(./assets/img/logo/logo_blue.png);
  }
}