.content__article {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 20px 0 0;
  padding: 60px 5vw 0;
  min-height: 56vw;

  @include mappy-query(portable) {
    padding-bottom: 90px;
    min-height: 100vh;
  }

  &::before {
    content: '';
    @include circle(56vw);
    @include abs(6vw);
    background-color: $color-blue;
    opacity: .2;
    z-index: 10;
    align-self: flex-end;

    @include mappy-query(portable) {
      @include circle(150vw);
      top: 60vw;
    }
  }

  figure {
    @include circle(36vw);
    position: relative;
    overflow: hidden;
    align-self: flex-start;

    @include mappy-query(portable) {
      @include circle(80vw);
    }

    img {
      position: absolute;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }

  &__text {
    display: inline-block;
    width: 420px;
    z-index: 12;
    align-self: flex-end;
    position: relative;
    top: -24vw;

    @include mappy-query(portable) {
      width: 100%;
      top: 0vw;

      a {
        @include rel(null, null, null, 50%);
        transform: translateX(-50%);
      }
    }

    h1 {
      @extend %h2;
    }
  }

  &:nth-child(even) {
    &::before { right: 8vw; }
    .content__article__text { right: 12vw }
    figure { 
      left: 6vw; 
      
      img { left: 0; }
    }

    @include mappy-query(portable) {
      &::before { right: -30vw; }
      .content__article__text { right: 0 }
      figure { left: -10vw; }
    }
  }

  &:nth-child(odd) {
    &::before, .content__article__text { 
      align-self: flex-start; 
    }

    &::before { left: 8vw }
    .content__article__text { left: 12vw }

    .content__article__text { padding: 10px 20px 0 6vw; }

    figure { 
      right: 6vw;
      align-self: flex-end;

      img { right: 0; }
    }

    @include mappy-query(portable) {
      &::before { left: -30vw; }
      .content__article__text { left: 0 }
      figure { right: -10vw; }
    }
  }
}