.diseases__list__item {
  list-style: none;

  article {
    width: 50%;
    padding: 40px;
    margin: 20px 0;
    
    @include mappy-query(portable) {
      width: 100%;
      padding: 20px;
    }
  }

  figure {
    img {
      width: 100%;
    }
  }

  h1 { 
    @extend %h1;
    margin-bottom: 0;
  }

  a {
    @extend %button-outline;
    font-size: .8em;
  }
}

