.answer {
  @include size(calc(50vw - 120px), 9vh);
  @extend %flex-center;
  float: right;
  color: white;
  background-color: $color-blue;
  border-radius: 40px;
  margin: 2vh 20px;
  text-transform: uppercase;
  clear: both;
  font-size: 1em;
  position: relative;

  &.is--active {
    transform: scale(1.1);
  }

  .is--wifi & {
    background-color: transparent;
    text-align: center;

    &::before {
      content: '';
      position: absolute;
      top: 0px;
      z-index: -1;
      border-top-color: $color-blue !important;
      border: 60px solid transparent;
    }

    @include mappy-query(desktop) {
      &::before { @include circle(50vw); }
      &:nth-child(3)::before { @include size(calc(50vw - 180px)) }
      &:nth-child(4)::before { @include size(calc(50vw - 340px)) }
      &:nth-child(5)::before { @include size(calc(50vw - 520px)) }
    }

    @include mappy-query(portable) {
      &::before { @include circle(120vw); }
      &:nth-child(3)::before { @include size(calc(120vw - 100px)) }
      &:nth-child(4)::before { @include size(calc(120vw - 240px)) }
      &:nth-child(5)::before { @include size(calc(120vw - 400px)) }
    }

    &:last-child::before {
      border: none;
      background-color: $color-blue;
      @include size(60px);
    }
  }

  @include mappy-query(portable) {
    width: 100%;
    min-height: 60px;
    align-items: center;
    margin: 2vh 0px;
  }
  
  label { 
    font-size: 1.2em;
    font-weight: 900; 
    padding: 10px 18px;
    line-height: 1.2;
  }

  &, label { cursor: pointer; }

  input { display: none }
  input:before {
    @include size;
    content: '';
    display: none;
    border: 3px solid white;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99999;
  }
  input:checked {
    &:before {
      display: block !important;
    }
  }
}

.wpt_test_form .question div:nth-child(2) {
  margin-top: 15vh;

  @include mappy-query(portable) {
    margin-top: 500px;
  }
}